import React from "react";
import TodoList from "./components/todoList";
import { Navigate, Route, Routes } from "react-router";
import { BrowserRouter as Router } from "react-router-dom";
import TsStudio from "pages/tsStudio/index";
import { User } from "pages/user";

function App() {
  return (
    <div className="App">
      <Router>
        <Routes>
          <Route path={"/tsStudio"} element={<TsStudio />} />
          <Route path={"/tsStudio/*"} element={<TsStudio />} />
          <Route path={"/project"} element={<TodoList />} />
          <Route path={"/project/:projectId/*"} element={<User />} />
          <Route path={"*"} element={<Navigate to={"/tsStudio"} replace />} />
          {/* , Navigate */}
        </Routes>
      </Router>
    </div>
  );
}

export default App;
