/**
 * search component
 * @returns {JSX.Element}
 */
import { Input } from "antd";
import { Select } from "antd";
import React from "react";
interface userList {
  name: string;
  id: number;
}

interface SearchTodoProps {
  userList: userList[];
  params: {
    name: string;
    personId: string;
  };
  setParams: (params: SearchTodoProps["params"]) => void;
}

const { Option } = Select;
export const SearchTodo = ({
  userList,
  params,
  setParams,
}: SearchTodoProps) => {
  const styleList = {
    width: "400px",
  };

  const onSearch = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setParams(Object.assign({}, params, { name: e.target.value }));
  };
  const handleChange = (value: string) => {
    // setProjectList(Object.assign({}, userList, { personId: value }));
    setParams(Object.assign({}, params, { personId: value }));
  };
  return (
    <div>
      <Input
        placeholder=""
        style={styleList}
        value={params.name}
        onChange={onSearch}
      />
      <Select
        value={params.personId}
        style={{
          width: 120,
        }}
        onChange={handleChange}
      >
        <Option value=""></Option>
        {userList.map((item) => {
          return (
            <Option value={item.id} key={item.id}>
              {item.name}
            </Option>
          );
        })}
      </Select>
    </div>
  );
};
