/**
 *   todoList 
 * @returns {JSX.Element}
 */

import { SearchTodo } from "./search-todo";
import { List } from "./list";
import { useEffect, useState } from "react";
import { useMount, useDebounce, useArray } from "../../useHooksUtils/useHooks";
import { Example } from "./example";
import { Button } from "antd";
import "./index.less";
// @ts-ignore
import style from "./index.less";
import { Link } from "react-router-dom";
const apiURL = process.env.REACT_APP_BASE_URL;

interface Person {
  name: string;
  age: number;
}
console.log("style", style);
const TodoList = () => {
  const [userList, setUserList] = useState([]);

  const [params, setParams] = useState({
    name: "",
    personId: "",
  });

  const [projectList, setProjectList] = useState([]);

  const debounce = useDebounce(params, 1000);

  const person: Person[] = [
    {
      name: "name1",
      age: 1,
    },
    {
      name: "name2",
      age: 2,
    },
  ];

  const { value, clear, removeIndex, add } = useArray(person);

  useMount(() => {
    console.log("value", value);
  });

  useEffect(() => {
    fetch(`${apiURL}/projectList`).then(async (response) => {
      if (response.ok) {
        response.json().then((res) => {
          setProjectList(res);
        });
      }
    });
  }, [debounce]);

  useMount(function () {
    fetch(`${apiURL}/users`).then(async (response) => {
      if (response.ok) {
        setUserList(await response.json());
      }
    });
  });

  const checkValue = () => console.log(value);
  const addValue = () => {
    add({ name: "name3", age: Math.random() });
  };
  const spliceValue = () => {
    removeIndex(0);
  };

  return (
    <>
      <Button type="primary" onClick={checkValue} className={"example"}>
        value add handle
      </Button>
      <div className={"test"}>
        123
        <div className={"test3"}>123</div>
      </div>
      <Button onClick={addValue} className={style.example}>
        value
      </Button>
      <Button onClick={spliceValue}>spliceValue</Button>
      <Link to={"123"}>link to 123</Link>
      {value.map((item) => {
        return (
          <div key={item.age}>
            {item.name}--- {item.age}
          </div>
        );
      })}
      <SearchTodo params={params} setParams={setParams} userList={userList} />
      <List userList={userList} projectList={projectList} />
      <Example />
    </>
  );
};
export default TodoList;
