/**
 * list component
 * @returns {JSX.Element}
 */
import React from "react";
import { Button } from "antd";

interface userList {
  name: string;
  id: number;
}
interface projectList {
  name: string;
  id: number;
  projectId: number;
}
interface listProps {
  userList: userList[];
  projectList: projectList[];
}
export const List = ({ userList, projectList }: listProps) => {
  return (
    <div>
      <Button type="primary">value add handle</Button>
      {projectList.map((item) => {
        return (
          <div key={item.id}>
            {item.name} {userList.find((i) => i.id === item.id)?.name}
          </div>
        );
      })}
    </div>
  );
};
